import { addFilter } from "@wordpress/hooks";
import { createHigherOrderComponent } from "@wordpress/compose";
import { Fragment } from "@wordpress/element";
import { InspectorControls } from "@wordpress/block-editor";
import { PanelBody, SelectControl } from "@wordpress/components";

/**
 * Lets webpack process CSS, SASS or SCSS files referenced in JavaScript files.
 * All files containing `style` keyword are bundled together. The code used
 * gets applied both to the front of your site and to the editor.
 *
 * @see https://www.npmjs.com/package/@wordpress/scripts#using-css
 */
import "./style.scss";

import "./view.js";

const ANIMATION_OPTIONS = [
  { label: "None", value: "" },
  { label: "Fade In Up", value: "fadeInUp" },
  { label: "Fade In Down", value: "fadeInDown" },
  { label: "Slide In Right", value: "slideInRight" },
  { label: "Zoom In", value: "zoomIn" },
  // Add more as needed
];

// Add attribute to all blocks
function addAnimationAttribute(settings, name) {
  // Only apply to core blocks (skip custom if needed)
  if (!name.startsWith("core/")) return settings;

  settings.attributes = {
    ...settings.attributes,
    dataAnimate: {
      type: "string",
      default: "",
    },
  };

  return settings;
}
addFilter(
  "blocks.registerBlockType",
  "custom/extend-core-blocks/add-animate-attribute",
  addAnimationAttribute
);

// Add UI to Inspector panel
const withAnimationControl = createHigherOrderComponent((BlockEdit) => {
  return (props) => {
    const { attributes, setAttributes, name } = props;

    if (!name.startsWith("core/")) return <BlockEdit {...props} />;

    return (
      <Fragment>
        <BlockEdit {...props} />
        <InspectorControls>
          <PanelBody title="Animation" initialOpen={false}>
            <SelectControl
              label="Data Animate"
              value={attributes.dataAnimate}
              options={ANIMATION_OPTIONS}
              onChange={(value) => setAttributes({ dataAnimate: value })}
            />
          </PanelBody>
        </InspectorControls>
      </Fragment>
    );
  };
}, "withAnimationControl");

addFilter(
  "editor.BlockEdit",
  "custom/extend-core-blocks/with-animation-control",
  withAnimationControl
);

// Apply attribute to save output
function applyDataAnimateProp(extraProps, blockType, attributes) {
  if (!blockType.name.startsWith("core/")) return extraProps;

  if (attributes.dataAnimate) {
    extraProps["data-animate"] = attributes.dataAnimate;
  }

  return extraProps;
}
addFilter(
  "blocks.getSaveContent.extraProps",
  "custom/extend-core-blocks/apply-data-animate",
  applyDataAnimateProp
);
